body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.checkedTodo {
  text-decoration: line-through;
  color: #656565; }

@media screen and (min-width: 960px) {
  .spacer {
    height: 50px; } }

.rbc-month-view .rbc-event {
  background-color: #2196f3; }

.rbc-time-view .rbc-event {
  background-color: #2196f3; }

.darkModeCal .rbc-toolbar {
  color: #fff; }
  .darkModeCal .rbc-toolbar button {
    color: inherit; }
  .darkModeCal .rbc-toolbar button.rbc-active {
    color: #000; }

.darkModeCal .rbc-agenda-view {
  color: indianred; }

.darkModeCal .rbc-date-cell {
  color: #fff; }

.darkModeCal .rbc-off-range {
  color: #999; }

.darkModeCal .rbc-date-cell.rbc-now.rbc-current {
  color: #000; }

.darkModeCal .rbc-month-header {
  color: #fff; }

.darkModeCal .rbc-time-header-cell {
  color: #fff; }
  .darkModeCal .rbc-time-header-cell .rbc-today {
    color: #000; }

.darkModeCal .rbc-time-column {
  color: #fff; }

.darkModeCal .rbc-time-content > div.rbc-day-slot.rbc-time-column.rbc-now.rbc-today {
  background-color: inherit; }

.darkModeCal .rbc-time-header > div.rbc-time-header-content > div.rbc-allday-cell > div.rbc-row-bg > .rbc-day-bg {
  background-color: inherit; }

